{this.maxWidth}px`\r\n        }\r\n\r\n        this.$refs.card.$el.appendChild(image)\r\n      })\r\n      .catch(() => {\r\n        this.missing = true\r\n\r\n        this.$emit('missing', true)\r\n      })\r\n      .finally(() => {\r\n        this.loading = false\r\n      })\r\n  },\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.card {\r\n  padding: 0 !important;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
